.card-cert {
  margin-right: auto;
  margin-bottom: 80px;
  margin-left: auto;
  width: 100%;
  max-width: 1920px;

  &__slider {
    margin-right: 20px;
    margin-left: 20px;

    .hero-slider__btn {
      color: #1078d3;
      background-color: #031367;
      opacity: 0.6;
    }
  }

  &__slide {
    margin-right: auto;
    margin-left: auto;
    border: 2px solid var(--Blue, #1078d3);
    border-radius: 10px;
    max-width: 400px;

    img {
      display: block;
      width: 100%;
    }
  }

  @include desktop {
    margin-bottom: 50px;
  }

  @include mobile {
    margin-bottom: 30px;
  }
}
